.displayModal {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    backdrop-filter: blur(1px);
    background-color: rgba(0, 0, 0, 0.25);
}

.modalBg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    border-radius: 15px;
    padding: 10px;
    box-sizing: border-box;
}

.closeButton {
    top: 20px;
    height: 25px;
    width: 25px;
    background-image: url('../../assets/close.svg');
}

.saveButton {
    bottom: 20px;
    height: 25px;
    width: 25px;
    background-image: url('../../assets/save.svg');
}

input {
    border-radius: 5px;
    border: 1px solid black;
    height: 10px;
    background-color: rgb(251, 251, 251);
    padding: 5px;
    margin: 10px 5px;
}

.buttonIcon {
    position: fixed;
    right: 20px;
    border: none;
    background-color: transparent;
}